export default {
  data() {
    return {
		remarkImg:'',
		content:'',
      }
  },

  onLoad(option) {
  },

  methods: {
    addImg() {
      let upLoader = new Uploader();
      upLoader.choose_and_upload(4).then(value => {
		  console.log(value,'-----------')
			this.remarkImg = [...this.remarkImg,...value]
      });
    },
  }
};
